.catalog-btn {
	color: var(--color-primary);
	background-color: transparent;
	border: none;
	cursor: pointer;
	font-family: var(--font-family-primary);
	font-size: 16px;
	line-height: 24px;
	display: inline-grid;
	grid-auto-flow: column;
	align-items: center;
	column-gap: 16px;
}

/* Img */
.catalog-btn__img {
	width: 20px;
	height: 20px;
}

@media (max-width: 414px) {
	.catalog-btn__img {
		width: 24px;
		height: 24px;
	}
}

/* Label */
@media (max-width: 414px) {
	.category-btn__label {
		display: none;
	}
}

/* Img list */
.catalog-btn.catalog-btn--expanded .catalog-btn__img-list {
	display: none;
}

/* Img cross */
.catalog-btn:not(.catalog-btn--expanded) .catalog-btn__img-cross {
	display: none;
}
