/* List */
.offer__list {
	padding: 0;
	margin: 0;
	display: inline-grid;
	grid-auto-flow: column;
	column-gap: 8px;
	width: 100%;
}

@media (max-width: 414px) {
	.offer__list {
		overflow-x: auto;
	}
}

.offer__list--scroll {
	overflow-x: auto;
}

/* Item */
.offer__item {
	list-style: none;
}

/* Heading */
.offer__heading {
	margin-bottom: 16px;
}

/* Img */
.offer__img {
	height: 256px;
	object-fit: fill;
}
