.header {
	background-color: #E4E4E4;
}

/* Body */
.header__body {
	display: flex;
	align-items: center;
	height: var(--header-height);
}

@media (max-width: 414px) {
	.header__body {
		justify-content: space-between;
	}
}

/* Logo */
.header__logo {
	margin-right: 32px;
}

@media (max-width: 414px) {
	.header__logo {
		margin: 0;
	}
}

/* Catalog btn */
.header__catalog-btn {
	margin-right: 74px;
}

@media (max-width: 414px) {
	.header__catalog-btn {
		margin: 0;
	}
}

/* Nav */
.header__nav {
	height: 100%;
}

@media (max-width: 414px) {
	.header__nav--hide-sm {
		display: none;
	}
}

/* Nav wrapper */
.header__nav-wrapper {
	display: grid;
	grid-auto-flow: column;
	column-gap: 64px;
	margin-left: auto;
}

@media (max-width: 414px) {
	.header__nav-wrapper {
		position: absolute;
	}
}

/* Search */
@media (max-width: 414px) {
	.header__search {
		display: none;
	}
}
